import React from 'react';
import { darkTheme } from './style';
import Logo from '../images/logo.svg';
import { scrollToTop, scrollToSection } from '../utils/scroll';

const menuStyles = {
  fontFamily: 'Garamond Premier Pro, YuMincho, serif',
  margin: '0 0.5em',
  color: '#fff'
};

const SectionMenu = () => (
  <nav
    id='menu'
    className='navbar fixed-top navbar-expand-lg navbar-dark'
    style={darkTheme}
  >
    <div className='container'>
      <a
        className='navbar-brand'
        href='#top'
        onClick={e => {
          e.preventDefault();
          scrollToTop();
        }}
      >
        <img src={Logo} alt='addict gym' height='40' />
      </a>
      <button
        className='navbar-toggler'
        type='button'
        data-toggle='collapse'
        data-target='#navbarSupportedContent'
        aria-controls='navbarSupportedContent'
        aria-expanded='false'
        aria-label='Toggle navigation'
      >
        <span className='navbar-toggler-icon'></span>
      </button>

      <div className='collapse navbar-collapse' id='navbarSupportedContent'>
        <ul className='navbar-nav ml-auto'>
          {[
            'About',
            'Facility',
            'Pricing',
            'Booking',
            'Trainer',
            'Access',
            'Contact'
          ].map((title, i) => {
            const selector = `#${title.toLocaleLowerCase()}`;

            return (
              <li key={i} className='nav-item'>
                <a
                  className='nav-link'
                  href={selector}
                  style={menuStyles}
                  onClick={e => {
                    e.preventDefault();
                    scrollToSection(selector);
                  }}
                >
                  {title}
                </a>
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  </nav>
);

export default SectionMenu;
