import React, { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import { pageStyles } from '../components/style';
import SectionMenu from '../components/section-menu';
import SectionMainvisual from '../components/section-mainvisual';
import SectionAbout from '../components/section-about';
import SectionFacility from '../components/section-facility';
import SectionPricing from '../components/section-pricing';
import SectionBooking from '../components/section-booking';
import SectionTrainer from '../components/section-trainer';
import SectionAccess from '../components/section-access';
import SectionContact from '../components/section-contact';
import { ParallaxProvider } from 'react-scroll-parallax';

const loadScript = scriptObject => {
  return new Promise((resolve, reject) => {
    const script = document.createElement('script');
    script.onload = () => resolve(true);
    script.onerror = () => reject(false);
    script.src = scriptObject.src;
    script.integrity = scriptObject.integrity;
    script.crossOrigin = scriptObject.crossOrigin;

    document.body.appendChild(script);
  });
};

const scripts = [
  {
    src: 'https://code.jquery.com/jquery-3.6.0.min.js',
    integrity: 'sha256-/xUj+3OJU5yExlq6GSYGSHk7tPXikynS7ogEvDej/m4=',
    crossOrigin: 'anonymous'
  },
  {
    src: 'https://cdn.jsdelivr.net/npm/popper.js@1.16.0/dist/umd/popper.min.js',
    integrity:
      'sha384-Q6E9RHvbIyZFJoft+2mJbHaEWldlvI9IOYy5n3zV9zzTtmI3UksdQRVvoxMfooAo',
    crossOrigin: 'anonymous'
  },
  {
    src:
      'https://stackpath.bootstrapcdn.com/bootstrap/4.5.0/js/bootstrap.min.js',
    integrity:
      'sha384-OgVRvuATP1z7JjHLkuOU7Xw704+h835Lr+6QL9UvYjZE3Ipu6Tp75j7Bh/kR0JKI',
    crossOrigin: 'anonymous'
  }
];

// @TODO from gatsby-config
const title = 'addict gym';
const url = 'https://addict-gym.com/';
const description =
  '東京・千駄ヶ谷にあるレンタルルームジム『addict gym』。贅沢な空間で上質なトレーニングを何度でも体験できます。';

const IndexPage = () => {
  useEffect(() => {
    (async () => {
      await loadScript(scripts[0]);
      await loadScript(scripts[1]);
      await loadScript(scripts[2]);
    })();
  });

  return (
    <ParallaxProvider>
      <div style={pageStyles}>
        <Helmet>
          <title>{title}</title>
          <meta name='description' content={description} />
          <meta property='og:title' content={title} />
          <meta property='og:type' content='website' />
          <meta property='og:url' content={url} />
          <meta property='og:description' content={description} />
          <meta property='og:image' content={`${url}ogp.png`} />
          <meta property='og:image:secure_url' content={`${url}ogp.png`} />
          <meta property='og:image:type' content='image/png' />
          <meta property='og:image:width' content={1200} />
          <meta property='og:image:height' content={630} />
          <meta property='og:locale' content='ja_JP' />
          <link
            rel='stylesheet'
            href='https://stackpath.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css'
            integrity='sha384-9aIt2nRpC12Uk9gS9baDl411NQApFmC26EwAOH8WgZl5MYYxFfc+NcPb1dKGj7Sk'
            crossOrigin='anonymous'
          />
        </Helmet>

        <SectionMenu />
        <SectionMainvisual />
        <SectionAbout />

        <div className='container-fluid d-none d-sm-block'>
          <div
            style={{
              marginTop: '-10vw',
              marginBottom: '-6vw',
              fontSize: '18vw',
              fontWeight: 800,
              textAlign: 'center',
              color: '#f7f3e9',
              pointerEvents: 'none'
            }}
          >
            addict gym
          </div>
        </div>

        <SectionFacility />
        <SectionPricing />
        <SectionBooking />
        <SectionAccess />
        <SectionContact />
      </div>
    </ParallaxProvider>
  );
};

export default IndexPage;
