const htmlBodySelector = 'html, body';

export const scrollToSection = (targetSelector, offset = 66) => {
  window.$(htmlBodySelector).animate({
    scrollTop: window.$(targetSelector).offset().top - offset
  });
};

export const scrollToTop = (offset = 0) => {
  window.$(htmlBodySelector).animate({
    scrollTop: offset
  });
};
